        'lang': String,},
    data(){
        return {
            editor:null,//ÊñáÊú¨ÁºñËæëÂô®
            isSave:true,//Êñá‰ª∂ÊîπÂä®Áä∂ÊÄÅÔºåÊòØÂê¶‰øùÂ≠ò
            oldValue:'' //‰øùÂ≠òÂêéÁöÑÊñáÊú¨
        }
    },
     mounted(){
      this.initEditor();  
    },
    methods:{
        initEditor(){
            let This = this
            // ÂàùÂßãÂåñÁºñËæëÂô®ÔºåÁ°Æ‰øùdomÂ∑≤ÁªèÊ∏≤Êüì
            this.editor = monaco.editor.create(this.$refs['containerFail'], {
