                language: This.lang,//ËØ≠Ë®ÄÊîØÊåÅËá™Ë°åÊü•ÈòÖdemo
                automaticLayout: true,//Ëá™Âä®Â∏ÉÂ±Ä
                theme:'vs-black' , //ÂÆòÊñπËá™Â∏¶‰∏âÁßç‰∏ªÈ¢òvs, hc-black, or vs-dark
                fontSize: 14,       //Â≠ó‰ΩìÂ§ßÂ∞è
                autoIndent:true,//Ëá™Âä®Â∏ÉÂ±Ä
                minimap: {
                    enabled: false, // ‰∏çË¶ÅÂ∞èÂú∞Âõæ
                },
                foldingStrategy: 'indentation',
            });
            
         
            this.editor.onKeyUp(() => {

                    this.$emit("editCode", this.editor.getValue());
                
                // console.log(this.editor.getValue())
            });
        }
    }
}
</script>

